@import "https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,400;9..40,700&display=swap";
* {
  box-sizing: border-box;
}

html, body, div, h1, h2, h3, p, ul, li, form, input, button {
  margin: 0;
  padding: 0;
}

body {
  font-family: DM Sans, sans-serif;
}

.home {
  -moz-column-gap: 40px;
  flex-flow: wrap;
  justify-content: center;
  gap: 86px 40px;
  margin: 40px 24px;
  display: flex;
}

.category-list {
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  width: 328px;
  height: 472px;
  padding-right: 20px;
  display: flex;
  overflow-y: auto;
}

.category-list::-webkit-scrollbar {
  width: 8px;
}

.category-list::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 12px;
}

.category-list__item {
  text-align: left;
  color: #1119;
  cursor: pointer;
  letter-spacing: -.36px;
  background-color: #0000;
  border: none;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  transition: color .3s ease-in;
}

.category-list__item:hover {
  color: #4f2ee8;
}

.category-list__item.active {
  color: #4f2ee8;
  text-transform: uppercase;
  letter-spacing: -.18px;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

.books {
  flex-direction: column;
  gap: 40px;
  width: 996px;
  display: flex;
}

.books-category {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

.books-category .books__category--books {
  row-gap: 40px;
}

.books__heading {
  color: #111;
  letter-spacing: -.92px;
  font-size: 48px;
  font-weight: 700;
  line-height: 52px;
}

.books__heading--highlight {
  color: #4f2ee8;
}

.books__category {
  flex-direction: column;
  display: flex;
}

.books__category--title {
  color: #b4afaf;
  text-transform: uppercase;
  letter-spacing: .42px;
  margin-bottom: 18px;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.books__category--books {
  flex-wrap: wrap;
  gap: 24px;
  margin-bottom: 32px;
  display: flex;
}

.books__book {
  cursor: pointer;
  width: 180px;
}

.books__book--cover {
  position: relative;
}

.books__book--cover:hover .books__book--cover-overlay {
  opacity: 1;
}

.books__book--cover img {
  border-radius: 8px;
  width: 180px;
  height: 256px;
}

.books__book--cover-overlay {
  opacity: 0;
  background-color: #eac645bf;
  border-radius: 8px;
  width: 100%;
  height: 99%;
  transition: all .5s ease-in;
  position: absolute;
  inset: 0;
}

.books__book--cover-overlay-text {
  color: #111;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: -.18px;
  border-radius: 8px;
  padding: 14px 28px;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.books__book--title {
  color: #111;
  letter-spacing: -.64px;
  margin-top: 14px;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.books__book--author {
  color: #b4afaf;
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.books .see-more-btn {
  color: #111;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: -.14px;
  background-color: #0000;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  align-self: flex-end;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  transition: color .25s ease-in, background-color .25s ease-in;
}

.books .see-more-btn:hover, .books .see-more-btn:focus {
  color: #fff;
  background-color: #4f2ee8;
}

.books .see-more-btn:focus {
  box-shadow: 0 4px 18px #00000026;
}

